<template>
  <div id="storyApp">
    <AppModal/>
    <Stories/>
  </div>
</template>

<script>
import Stories from "@/components/Stories";
import storyData from "@/assets/stories.json";
import { mapMutations } from "vuex";

export default {
  name: "Story",
  props: {
    storyData: {
      type: Array,
      default: () => storyData
    },
    options: {
      type: Object,
      default: () => ({
        fonts: {
          standart: {
            label: "Standart",
            font: "300 50px Arial, Helvetica, sans-serif",
            stroke: true
          },
          daktilo: {
            label: "Daktilo",
            font: "500 50px Arial, Helvetica, sans-serif",
            stroke: false
          },
          guclu: {
            label: "Gl",
            font: "700 italic 50px Arial, Helvetica, sans-serif",
            stroke: false
          }
        },
        activeFont: "standart",
        colors: ["#fff", "#fbac00", "#fd6532", "#2697fd", "#16fda6"],
        activeColor: 0,
        backgrounds: {
          blue: {
            type: "linear",
            colors: ["#0e277c", "#07164c"]
          },
          yellow: {
            type: "linear",
            colors: ["#0e277c", "#07164c"]
          }
        },
        activeBackground: "blue"
      })
    }
  },
  components: {
    Stories
  },
  methods: {
    ...mapMutations(["setStories", "setOptions"])
  },
  watch: {
    storyData(data) {
      this.setStories(data);
    },
    options(newOptions, oldOptions) {
      this.setOptions({ ...oldOptions, ...newOptions });
    }
  },
  mounted() {
    this.setStories(this.storyData);
    this.setOptions(this.options);
  }
};
</script>

<style lang="scss">
@import "@/assets/scss/_main.scss";
@import "@/assets/scss/_utils.scss";
@import "@/assets/scss/_animation.scss";
</style>
