<template>
  <div class="circle-box" :class="variants">
    <a
      class="circle-box__close"
      v-if="closed"
      href="#"
      @click.prevent="closeClicked"
    >
      <Icon name="Close" fill="#000" />
    </a>
    <div
      class="circle-box__content"
      v-on="$listeners"
      :style="{ width: size, height: size }"
    >
      <img
        v-if="avatar"
        class="circle-box__avatar"
        :src="require(`@/assets/images/${avatar}`)"
      />
    </div>
    <p class="circle-box__title" v-if="title">{{ title }}</p>
  </div>
</template>

<script>
export default {
  name: "CircleBox",
  props: {
    click: {
      type: Function
    },
    closed: {
      type: Boolean,
      default: true
    },
    avatar: {
      type: String
    },
    title: {
      type: String,
      default: "Title"
    },
    size: {
      type: String,
      default: "100px"
    }
  },
  computed: {
    variants() {
      return {};
    }
  },
  methods: {
    closeClicked() {
      this.$emit("close");
    }
  }
};
</script>

<style lang="scss">
.circle-box {
  position: relative;
  display: inline-block;
  text-align: center;
  transition: all 1s ease-in-out;

  &__close {
    position: absolute;
    top: 0px;
    right: 0;
  }

  &__content {
    padding: 5px;
    border-radius: 100px;
    border: 1px solid #eee;
    overflow: hidden;
  }

  &__avatar {
    width: 100%;
    height: 100%;
    border: none;
    border-radius: 100px;
  }

  &__title {
    margin-top: 8px;
    font-size: 13px;
    color: rgb(46, 53, 57);
  }
}
</style>
