<template>
  <section class="stories">
    <CircleBox
      class="stories__item bounceIn -m-right-15"
      @click="createStory"
      title="Yeni Story"
      :closed="false"
    />
    <transition-group
      class="stories__wrapper"
      tag="div"
      enter-active-class="bounceIn"
      leave-active-class="bounceOut"
    >
      <CircleBox
        class="stories__item bounceIn"
        v-for="story in stories"
        :key="story.id"
        @close="removeStory(story.id)"
        @click="openStory(story.id)"
        :avatar="story.avatar"
        :title="story.title"
      />
    </transition-group>
  </section>
</template>

<script>
import { mapState, mapMutations } from "vuex";

export default {
  name: "Stories",
  props: {
    value: {
      type: Array,
      default: () => []
    }
  },
  computed: {
    ...mapState(["stories"])
  },
  methods: {
    ...mapMutations(["removeStory", "openStory", "createStory"])
  }
};
</script>

<style lang="scss">
.stories {
  display: flex;
  align-items: center;
  justify-items: center;
  overflow: auto;
  white-space: nowrap;
  padding: 20px 0;

  &__item {
    + .stories__item {
      margin-left: 15px;
    }
  }
}
</style>
