<template>
  <div :class="variants">
    <span class="progress__wait" :style="style"/>
  </div>
</template>

<script>
export default {
  name: "Progress",
  data() {
    return {
      time: 0
    };
  },
  props: {
    wait: {
      type: Number
    },
    start: {},
    end: {}
  },
  computed: {
    style() {
      return {
        width: this.end ? "100%" : `${(100 / this.wait) * this.time}%`
      };
    },
    variants() {
      return {
        progress: true,
        "-end": this.end
      };
    }
  },
  watch: {
    start() {
      if (this.start) {
        this.startProgress();
      }
    }
  },
  methods: {
    startProgress() {
      const pi = setInterval(() => {
        if (this.time === this.wait) {
          this.time = 0;
          clearInterval(pi);
          this.$emit("end");
        } else {
          this.time += 1;
        }
      }, 1000);
    }
  },
  mounted() {
    if (this.start) {
      this.startProgress();
    }
  }
};
</script>

<style lang="scss">
.progress {
  height: 3px;
  background-color: rgba(255, 2555, 255, 0.4);
  width: 100%;
  position: relative;
  overflow: hidden;

  & + & {
    margin-left: 5px;
  }

  &__wait {
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    background-color: #fff;
    width: 0%;
    transition: 1s linear;
  }
}
</style>
