<template>
  <label :for="_uid" class="file">
    <input
      type="file"
      :id="_uid"
      ref="fileInput"
      class="file__input"
      v-bind="$attrs"
      @change="changeFileInput"
    >
    <div v-if="files.length === 0" class="file__view"></div>
    <img
      class="file__view"
      v-for="(file, i) in files"
      :key="i"
      :src="URL.createObjectURL(file)"
      :alt="file.name"
    >
  </label>
</template>

<script>
export default {
  name: "File",
  data() {
    return {
      files: []
    };
  },
  computed: {
    URL() {
      return window.URL || window.webkitURL;
    }
  },
  methods: {
    changeFileInput() {
      this.files = this.$refs.fileInput.files;
      this.$emit("change", this.files);
    }
  }
};
</script>

<style lang="scss">
.file {
  position: relative;
  z-index: 99;
  display: flex;
  align-items: center;
  overflow: hidden;

  &__input {
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0;
  }

  &__view {
    width: 30px;
    height: 30px;
    border-radius: 5px;
    border: 1px solid #fff;
    cursor: pointer;

    + .file__view {
      margin-left: 10px;
    }
  }
}
</style>
