<template>
  <transition>
    <section class="story" tabindex="1" @keydown.left="prevMedia" @keydown.right="nextMedia">
      <header class="story__header">
        <div class="story__progress">
          <Progress
            v-for="index in activeStoryItems.length"
            :key="index"
            :wait="5"
            :start="index === progressIndex"
            :end="progressIndex > index"
            @end="nextMedia"
          />
        </div>
        <div class="story__info">
          <CircleBox :closed="false" :avatar="story.avatar" :title="story.title" size="49px"/>
          <Button @click="toggleModal">
            <Icon name="Close" fill="#fff" width="28" height="28"/>
          </Button>
        </div>
      </header>
      <img v-if="media && media.type === 'image'" class="story__media" :src="media.mediaUrl">
      <footer class="story__footer">
        <Button type="circle" size="md" class="-m-right-10">
          <Icon name="Camera"/>
        </Button>
        <InputText placeholder="Mesaj Gnder">
          <Button>
            <Icon name="More"/>
          </Button>
        </InputText>
        <Button size="md">
          <Icon name="Send"/>
        </Button>
      </footer>
      <Button @click="prevMedia" class="story__navigation story__navigation--prev"/>
      <Button @click="nextMedia" class="story__navigation story__navigation--next"/>
    </section>
  </transition>
</template>

<script>
import { mapMutations, mapGetters } from "vuex";
export default {
  name: "StoryModal",
  data() {
    return {
      mediaIndex: 0
    };
  },
  computed: {
    ...mapGetters({
      story: "activeStory",
      activeStoryItems: "activeStoryItems"
    }),
    progressIndex() {
      return this.mediaIndex + 1;
    },
    media() {
      return this.activeStoryItems[this.mediaIndex];
    }
  },
  watch: {
    step(newVal, oldVal) {
      // if (newVal < oldVal) {
      //   this.animationDirection = "Right";
      // } else {
      //   this.animationDirection = "Left";
      // }
    }
  },
  methods: {
    ...mapMutations(["toggleModal", "prevStory", "nextStory"]),
    prevMedia() {
      if (this.mediaIndex === 0) {
        this.prevStory();
      } else {
        this.mediaIndex -= 1;
      }
    },
    nextMedia() {
      if (this.progressIndex === this.activeStoryItems.length) {
        this.mediaIndex = 0;
        this.nextStory();
      } else {
        this.mediaIndex += 1;
      }
    }
  },
  mounted() {}
};
</script>

<style lang="scss">
.story {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-flow: column;
  align-content: stretch;
  justify-content: space-between;

  &__header {
    padding: 10px;
  }

  &__progress {
    display: flex;
    margin-bottom: 10px;
  }

  &__info {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }

  &__content {
    height: 100%;
  }

  &__media {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    object-fit: cover;
  }

  &__footer {
    padding: 10px 0;
    display: flex;
    align-items: center;
  }

  &__navigation {
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    background-color: transparent;
    z-index: -1;

    &:focus {
      outline: none;
      border: none;
    }

    &--prev {
      left: 0;
    }

    &--next {
      right: 0;
    }
  }
}
</style>
