<template>
  <section class="video-create">
    <header class="video-create__header">
      <Button>
        <Icon name="SmileEmoji"/>
      </Button>
      <Button type="circle" class="-m-left-15">
        <Icon name="Edit"/>
      </Button>
      <Button class="-position-right" @click="toggleModal">
        <Icon name="Close" width="30px" height="30px"/>
      </Button>
    </header>
    <video id="video" playsinline webkit-playsinline/>
    <canvas id="canvas"/>
    <div class="video-create__footer">
      <div class="video-create__capture">
        <File
          multiple
          accept="file_extension|audio/*|video/*|image/*|media_type"
          @change="fileChange"
        />
        <Button type="create" @click="saveMedia" class="video-create__btn-capture">
          <Icon name="Right"/>
        </Button>
        <Button
          type="circle"
          class="-position-right"
          v-if="videoDevices.length > 1"
          @click.prevent="changeCam"
        >
          <Icon name="Change"/>
        </Button>
      </div>
    </div>
  </section>
</template>

<script>
import { mapMutations } from "vuex";

export default {
  name: "VideoStory",
  data() {
    return {
      stream: null,
      activeCamIndex: 0,
      videoDevices: [],
      img: null,
      canvas: null,
      context: null
    };
  },
  computed: {
    activeCam() {
      const device = this.videoDevices[this.activeCamIndex];
      if (device) {
        return device.deviceId;
      }
      return 0;
    }
  },
  watch: {
    async activeCamIndex() {
      this.stopStream();
      this.stream = await this.streamVideo();
    }
  },
  methods: {
    ...mapMutations(["toggleModal"]),
    async streamVideo() {
      let stream = null;
      const video = document.getElementById("video");
      const vendorUrl = window.URL || window.webkitURL;

      if (navigator.mediaDevices && navigator.mediaDevices.getUserMedia) {
        stream = await navigator.mediaDevices.getUserMedia({
          video: {
            deviceId: {
              exact: this.activeCam
            }
          },
          audio: false
        });

        stream.stop = function() {
          this.getAudioTracks().forEach(track => {
            track.stop();
          });
          this.getVideoTracks().forEach(track => {
            track.stop();
          });
          video.pause();
          stream = null;
        };

        try {
          video.srcObject = stream;
        } catch (e) {
          video.src = vendorUrl.createObjectURL(stream);
        }
        video.play();
      }

      video.addEventListener(
        "play",
        () => {
          this.drawCanvas(video);
        },
        false
      );
      return stream;
    },
    drawCanvas(media) {
      const width = window.innerWidth;
      const height = window.innerHeight;
      this.canvas.width = width;
      this.canvas.height = height;
      const context = this.context;
      context.clearRect(0, 0, width, height);
      context.drawImage(media, 0, 0, width, height);
      context.fillStyle = "rgba(0, 0, 0, .5)";
      context.fillRect(0, 0, width, height);
      context.fillStyle = "red";
      context.font = "300 30px Arial, Helvetica, sans-serif";
      context.textAlign = "center";
      context.textBaseline = "middle";
      context.fillText("Deneme", width / 2, height / 2, width);
      setTimeout(() => {
        if (this.stream) {
          this.drawCanvas(media);
        }
      }, 10);
    },
    changeCam() {
      if (this.activeCamIndex + 1 === this.videoDevices.length) {
        this.activeCamIndex = 0;
      } else {
        this.activeCamIndex += 1;
      }
    },
    stopStream() {
      if (this.stream) {
        this.stream.stop();
        this.stream = null;
      }
    },
    fileChange(files) {
      const file = files[0];
      if (file.type.match("image.*")) {
        const reader = new FileReader();
        reader.readAsDataURL(file);
        reader.onload = evt => {
          this.stopStream();
          if (evt.target.readyState == FileReader.DONE) {
            const img = this.img;
            img.src = evt.target.result;
            img.onload = () => this.drawCanvas(img);
          }
        };
      }
    },
    saveMedia() {},
    async init() {
      const devices = await navigator.mediaDevices.enumerateDevices();
      this.videoDevices = devices.filter(
        device => device.kind === "videoinput"
      );
      this.img = new Image();
      this.canvas = document.getElementById("canvas");
      this.context = this.canvas.getContext("2d");
      this.stream = await this.streamVideo();
    }
  },
  mounted() {
    this.init();
  },
  destroyed() {
    this.stopStream();
  }
};
</script>

<style lang="scss" scoped>
