    overflow: hidden;
  }

  &__footer {
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

#video,
#canvas {
  position: fixed;
  left: 0;
