<template>
  <div class="modal" v-if="modalVisible" tabindex="0" @keyup.esc="toggleModal">
    <transition appear enter-active-class="fadeIn" leave-active-class="fadeOut">
      <div class="modal__content" v-if="modalName">
        <component v-bind="modal.props" :is="modalName"/>
      </div>
    </transition>
  </div>
</template>

<script>
import * as modals from "@/components/modal";
import { mapState, mapMutations } from "vuex";

export default {
  name: "AppModal",
  components: { ...modals },
  computed: {
    ...mapState(["modal", "modalVisible"]),
    modalName() {
      const { name } = this.modal;
      if (!name || !this.$options.components[name]) {
        console.warn(`${name} Modal not found`);
        return null;
      }
      return name;
    }
  },
  methods: {
    ...mapMutations(["toggleModal"])
  }
};
</script>

<style lang="scss">
.modal {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100%;
  height: 100%;
  margin: 0 auto;
  z-index: 99;
  background-color: #252525;

  &__content {
    position: relative;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
  }
}
</style>
