<template>
  <button :class="variants" v-bind="$attrs" v-on="$listeners">
    <slot/>
  </button>
</template>

<script>
export default {
  name: "Button",
  props: {
    type: {
      type: String
    },
    size: {
      type: String
    }
  },
  computed: {
    variants() {
      return {
        btn: true,
        [`-${this.type}`]: !!this.type,
        [`-${this.size}`]: !!this.size
      };
    }
  }
};
</script>

