<template>
  <section class="text-story">
    <header class="text-story__header">
      <Button>
        <Icon name="SmileEmoji"/>
      </Button>
      <Button type="circle" class="-m-left-15">
        <Icon name="Edit"/>
      </Button>
      <Button
        class="-position-center"
        type="radius"
        :style="{font: selectedFont.font}"
        @click.prevent="changeFontStyle"
      >{{ selectedFont.label }}</Button>
      <Button v-if="openKeyboard">Tamam</Button>
      <Button v-else @click="toggleModal">
        <Icon name="Close" width="30px" height="30px"/>
      </Button>
    </header>
    <canvas id="canvas"/>
    <textarea
      id="text-area"
      ref="textArea"
      unselectable="on"
      @focus="openKeyboard = true"
      @blur="openKeyboard = false"
      @input="(e) => text = e.target.value"
    />
    <div class="text-story__footer">
      <div class="text-story__options">
        <Button
          type="color"
          v-for="(color, i) in options.colors"
          :key="i"
          :style="{backgroundColor: color}"
          @click.prevent="changeColor(i)"
        >
          <Icon v-if="i === options.activeColor" name="Paint" fill="gray"/>
        </Button>
      </div>
      <div class="text-story__capture" v-if="!openKeyboard">
        <img v-if="imgSrc" :src="imgSrc" class="text-story__img">
        <Button type="create" @click="saveImage" class="text-story__btn-capture">
          <Icon name="Right"/>
        </Button>
      </div>
    </div>
  </section>
</template>

<script>
import { mapMutations, mapState } from "vuex";

export default {
  name: "TextStory",
  data() {
    return {
      type: "text",
      text: null,
      openKeyboard: false,
      canvas: null,
      ctx: null,
      imgSrc: null
    };
  },
  computed: {
    ...mapState(["options"]),
    selectedFont() {
      return this.options.fonts[this.options.activeFont];
    },
    selectedBackground() {
      return this.options.backgrounds[this.options.activeBackground];
    },
    selectedColor() {
      return this.options.colors[this.options.activeColor];
    }
  },
  watch: {
    text() {
      this.drawCanvas();
    },
    selectedBackground() {
      this.drawCanvas();
    },
    selectedFont() {
      this.drawCanvas();
    },
    selectedColor() {
      this.drawCanvas();
    }
  },
  methods: {
    ...mapMutations(["toggleModal"]),
    changeColor(index) {
      this.options.activeColor = index;
    },
    changeFontStyle() {
      const fontKeys = Object.keys(this.options.fonts);
      const activeKeyIndex = fontKeys.findIndex(
        f => this.options.fonts[f].label === this.selectedFont.label
      );
      if (activeKeyIndex + 1 === fontKeys.length) {
        this.options.activeFont = fontKeys[0];
      } else {
        this.options.activeFont = fontKeys[activeKeyIndex + 1];
      }
    },
    drawCanvas() {
      const text = this.text || "Yazmak iin dokunun";
      const canvas = document.getElementById("canvas");
      const width = window.innerWidth;
      const height = window.innerHeight;
      canvas.width = width;
      canvas.height = height;
      const ctx = canvas.getContext("2d");

      this.canvas = canvas;
      this.ctx = ctx;

      const { colors, type } = this.selectedBackground;

      var grd = ctx.createLinearGradient(0, 0, 200, 200);
      colors.forEach((color, i) => {
        grd.addColorStop(i, color);
        grd.addColorStop(i, color);
      });

      ctx.fillStyle = grd;
      ctx.fillRect(0, 0, width, height);
      ctx.font = this.selectedFont.font;
      ctx.fillStyle = this.selectedColor;
      ctx.strokeStyle = this.selectedColor;
      ctx.textAlign = "center";
      ctx.textBaseline = "middle";
      const lineheight = 40;
      const x = height / 2;
      const lines = text.split("\n");
      const length = lines.length === 1 ? 2 : lines.length;
      const y = height / length;

      for (var i = 0; i < lines.length; i++) {
        if (this.selectedFont.stroke) {
          ctx.strokeText(lines[i], width / 2, y + i * lineheight, width - 50);
        } else {
          ctx.fillText(lines[i], width / 2, y + i * lineheight, width - 50);
        }
      }
    },
    saveImage() {
      this.imgSrc = this.canvas.toDataURL();
    }
  },
  mounted() {
    this.drawCanvas();
    this.$nextTick(() => {
      window.addEventListener("resize", () => {
        this.drawCanvas();
      });
    });
  }
};
</script>

