<template>
  <div :class="variants">
    <div class="tab__content">
      <TextStory v-if="currentType === 0" @keyboard="toggleKeyboard"/>
      <VideoStory v-if="currentType === 1"/>
    </div>
    <div class="tab__footer">
      <nav id="tab-nav" class="tab__nav" :style="navStyles">
        <Button
          :class="i === currentType ? 'tab__btn -active' : 'tab__btn'"
          v-for="(type, i) in types"
          :key="i"
          :id="`type${i}`"
          @click.prevent="currentType = i"
        >{{ type }}</Button>
      </nav>
    </div>
  </div>
</template>

<script>
import TextStory from "@/components/createStory/TextStory";
import VideoStory from "@/components/createStory/VideoStory";

let xDown = null;
let yDown = null;

export default {
  name: "NewStory",
  components: {
    TextStory,
    VideoStory
  },
  data() {
    return {
      types: ["Yaz", "Normal"],
      currentType: -1,
      openKeyboard: false
    };
  },
  computed: {
    currentTabPosition() {
      const $currentNav = document.getElementById(`type${this.currentType}`);
      if (!$currentNav) return;
      const centerPos = window.innerWidth / 2;
      // const positionLeft = $currentNav.offsetLeft;
      const width = $currentNav.offsetWidth;
      return centerPos - $currentNav.offsetLeft - width / 2;
    },
    navStyles() {
      return {
        transform: `translateX(${this.currentTabPosition}px)`
      };
    },
    variants() {
      return {
        tab: true,
        "-keyboard-active": this.openKeyboard
      };
    }
  },
  methods: {
    getTouches(evt) {
      return evt.touches || evt.originalEvent.touches;
    },
    handleTouchStart(evt) {
      const firstTouch = this.getTouches(evt)[0];
      xDown = firstTouch.clientX;
      yDown = firstTouch.clientY;
    },
    handleTouchMove(evt) {
      if (!xDown || !yDown) {
        return;
      }

      var xUp = evt.touches[0].clientX;
      var yUp = evt.touches[0].clientY;

      var xDiff = xDown - xUp;
      var yDiff = yDown - yUp;

      if (Math.abs(xDiff) > Math.abs(yDiff)) {
        if (xDiff > 0) {
          this.currentType += 1;
        } else {
          this.currentType -= 1;
        }
      }
      xDown = null;
      yDown = null;
    },
    toggleKeyboard(keyboardStatus) {
      this.openKeyboard = !keyboardStatus;
    }
  },
  mounted() {
    this.currentType = 0;
    const $nav = document.getElementById("tab-nav");
    $nav.addEventListener("touchstart", this.handleTouchStart, false);
    $nav.addEventListener("touchmove", this.handleTouchMove, false);
  }
};
</script>

<style lang="scss">
.tab {
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  &__nav {
    display: flex;
    align-items: center;
    transition: all 0.3s;
  }

  &__btn {
    padding: 10px;
    font-size: 18px;
    transition: all 0.3s;
    opacity: 0.4;
    transform-origin: center;
    transform: scale(0.8);

    &.-active {
      opacity: 1;
      transform: scale(1);
    }
  }

  &__content {
    flex: 1;
  }

  &__footer {
    position: relative;
    z-index: 1;
  }

  &.-keyboard-active & {
    &__btn-send {
      display: none;
    }

    &__footer {
      display: none;
    }
  }
}
</style>
